body {
  color: #000;
}

button {
  position: relative;

  box-sizing: border-box;
  display: block;
  margin: 0 auto;
  padding: 10px;
  font-size: 20px;

  background-color: red;
  color: white;
  border: 1px solid red;
  border-radius: 5%;

  cursor: pointer;
}

button:hover {
  opacity: 0.6;
}

.container {
  padding-top: 100px;
}

.popover {
  position: absolute;
  width: 350px;
  box-sizing: border-box;
  border: 1px solid black;
  border-radius: 4%;
}

.popover-title {
  font-weight: 700;
  padding: 5px;
  background-color: grey;
}

.popover-content {
  padding: 5px;
}
